@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700;800;900&display=swap");
@keyframes gradient {
  0% {
    transform: translateX(-100%) skew(45deg);
  }
  100% {
    transform: translateX(100%) skew(45deg);
  }
}
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  font-size: 100%;
  vertical-align: baseline;
  background: transparent;
  box-sizing: border-box;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
menu,
nav,
section {
  display: block;
}

:focus {
  outline: 0;
}

ol,
ul {
  list-style: none;
}

a {
  text-decoration: none;
  display: block;
}

a:hover {
  text-decoration: none;
}

input,
textarea {
  border: none;
  outline: none;
  padding: 0;
  margin: 0;
}

label,
button,
input[type=submit],
input[type=button] {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

body {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

img {
  max-width: 100%;
  vertical-align: middle;
  height: auto;
}

body {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

button,
input[type=submit] {
  -webkit-appearance: none;
  -moz-appearance: none;
}

* {
  box-sizing: border-box;
}

.fp-watermark {
  display: none;
}

body {
  position: relative;
  min-width: 320px;
  margin: auto;
  background-image: linear-gradient(180deg, #F5F5FA 0%, #F2DAD5 44.15%, #A1ABD6 100%);
  color: #231D4F;
  font-family: "Poppins", sans-serif;
  font-size: 15px;
}

h1 {
  font-size: 40px;
  font-weight: 400;
  letter-spacing: -0.5px;
}

h3 {
  font-size: 28px;
  font-weight: 500;
}

p,
li {
  font-size: 15px;
  color: #848199;
}

.container {
  margin: 0 auto;
  max-width: 966px;
  width: 70.7174231332%;
}

section {
  position: relative;
}

.logo {
  width: 137px;
  cursor: pointer;
}
.logo img {
  width: 100%;
  object-fit: contain;
}

.btn {
  font-size: 14px;
  font-weight: 500;
  line-height: 1.3;
  padding: 10px 30px;
  min-width: 100px;
  width: max-content;
  max-width: 300px;
  background-color: #BB6BD9;
  color: #FFFFFF;
  border-radius: 25px;
  cursor: pointer;
  transition: 0.3s ease-in-out all;
  text-align: center;
}
.btn:hover {
  background-color: #A847CC;
}
.btn_blue {
  background-color: #231D4F;
}

.menu {
  width: 32px;
  height: 32px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.menu__icon {
  width: 22px;
  height: 15px;
  position: relative;
  display: flex;
  align-items: center;
  cursor: pointer;
}
.menu__icon span {
  position: absolute;
  display: block;
  width: 100%;
  height: 2px;
  background-color: #231D4F;
}
.menu__icon span:nth-child(1) {
  width: 80%;
  transform: translateY(-5px);
}
.menu__icon span:nth-child(2) {
  width: 80%;
  transform: translateY(5px);
}

.header {
  padding: 28px 0;
}
.header__container {
  max-width: 1266px;
  width: 92.6793557833%;
}
.header__row {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.plans {
  padding: 40px 0 70px;
}
.plans__container {
  padding-right: 30px;
  width: 88%;
}
.plans__title {
  margin-bottom: 25px;
}
.plans__descr-row {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 8.4722222222vw;
  padding-right: 10px;
}
.plans__descr {
  font-size: 18px;
  font-weight: 500;
}
.plans__list {
  display: flex;
  gap: 34px;
  padding: 20px 45px 20px 68px;
  background-color: rgba(255, 255, 255, 0.64);
  border-radius: 26px;
  margin-top: 90px;
}
.plans__item {
  padding: 20px 0;
  width: 28.2%;
  display: flex;
  flex-direction: column;
  border-radius: 26px;
  transition: 0.3s ease-in-out all;
}
.plans__item:hover {
  transform: translateY(-10px);
}
.plans__item.popular {
  background-color: #231D4F;
  box-shadow: 0px 42px 34px rgba(82, 67, 194, 0.295755);
  margin-top: -47px;
  padding: 20px 30px;
  flex: 1 0 auto;
}
.plans__item.popular:hover .plans__item-popular::after {
  background-image: linear-gradient(to right, rgba(255, 255, 255, 0) 0%, rgba(255, 255, 255, 0) 40%, rgba(255, 255, 255, 0.7) 100%);
  animation: gradient 0.7s linear forwards;
}
.plans__item.popular .plans__item-price {
  color: #FFFFFF;
}
.plans__item.popular .plans__item-price span {
  color: inherit;
}
.plans__item.popular .plans__item-name, .plans__item.popular .plans__item-descr, .plans__item.popular .plans__item-benefits {
  color: #FFFFFF;
}
.plans__item.popular .plans__item-benefits li {
  color: inherit;
}
.plans__item.popular .plans__item-btn {
  background-color: #BB6BD9;
}
.plans__item.popular .plans__item-btn:hover {
  background-color: #A847CC;
}
.plans__item.popular .plans__item-btn {
  max-width: none;
}
.plans__item-price {
  font-size: 17px;
  color: #848199;
  margin-bottom: 20px;
  transition: 0.5s ease-in-out all;
}
.plans__item-price span {
  font-size: 36px;
  color: #231D4F;
  font-weight: 700;
  line-height: 1.2;
  margin-right: 4px;
}
.plans__item-price.hide {
  opacity: 0;
  transform: translateY(50%);
}
.plans__item-name {
  margin-bottom: 6px;
}
.plans__item-descr {
  margin-bottom: 15px;
}
.plans__item-benefits {
  margin-bottom: 20px;
}
.plans__item-benefits li {
  list-style-position: outside;
  position: relative;
  padding-left: 28px;
  align-items: center;
  display: flex;
}
.plans__item-benefits li:not(:last-child) {
  margin-bottom: 8px;
}
.plans__item-benefits li::before {
  content: "";
  position: absolute;
  left: 0;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background-color: rgba(82, 67, 194, 0.15);
}
.plans__item-benefits li::after {
  content: "";
  position: absolute;
  left: 0;
  width: 20px;
  height: 20px;
  background: url("../images/check.svg") no-repeat center/11px;
}
.plans__item-btn {
  margin-top: auto;
  width: 100%;
  max-width: 207px;
  font-size: 15px;
  line-height: 1.667;
}
.plans__item-popular {
  padding: 5px 16px;
  background-color: #403879;
  color: #BB6BD9;
  align-self: flex-end;
  border-radius: 13px;
  margin-bottom: 20px;
  font-size: 10px;
  line-height: 1.7;
  position: relative;
  overflow: hidden;
}
.plans__item-popular:after {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  transform: skew(45deg);
}

.switcher {
  display: flex;
  position: relative;
  background-color: #FFFFFF;
  border-radius: 22px;
  cursor: pointer;
}
.switcher::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 110px;
  border-radius: inherit;
  background-color: #BB6BD9;
  transition: 0.7s ease-in-out all;
}
.switcher__btn {
  width: 110px;
  padding: 13px 0;
  border-radius: 22px;
  font-size: 12px;
  font-weight: 500;
  text-align: center;
  position: relative;
  transition: 0.7s ease-in-out all;
}
.switcher.active-1::before {
  transform: translateX(0);
}
.switcher.active-1 .monthly {
  color: #FFFFFF;
}
.switcher.active-2::before {
  transform: translateX(100%);
}
.switcher.active-2 .yearly {
  color: #FFFFFF;
}

@media only screen and (max-width: 1025px) {
  .container {
    width: 80%;
  }
  .header__container {
    width: 90%;
  }
  .plans__container {
    padding-right: 0;
  }
  .plans__descr-row {
    padding-right: 0;
  }
  .plans__list {
    gap: 30px;
    padding: 20px 30px;
    flex-wrap: wrap;
    margin-top: 50px;
  }
  .plans__item {
    width: calc(50% - 15px);
    padding: 20px 30px;
  }
  .plans__item.popular {
    flex: initial;
    margin: 0 auto;
  }
}
@media only screen and (max-width: 769px) {
  .container {
    width: 100%;
    padding: 0 15px;
  }
  .plans__list {
    gap: 30px;
    padding: 8px;
  }
}
@media only screen and (max-width: 651px) {
  .plans {
    padding-bottom: 65px;
  }
  .plans__title {
    margin-bottom: 20px;
  }
  .plans__descr-row {
    flex-direction: column;
    align-items: flex-start;
    gap: 35px;
  }
  .plans__switcher {
    align-self: center;
  }
  .plans__list {
    gap: 48px;
    flex-direction: column;
    padding: 32px 8px 8px;
    margin-top: 32px;
  }
  .plans__item {
    width: 100%;
    padding: 0 20px;
  }
  .plans__item-btn {
    max-width: none;
  }
  .plans__item-popular {
    align-self: center;
  }
}
@media only screen and (max-width: 551px) {
  .header__btn {
    display: none;
  }
}